import {Component, Input, OnChanges, OnInit, SimpleChanges} from '@angular/core';
import { LoadingController } from '@ionic/angular';

@Component({
  selector: 'app-kroba-loader',
  templateUrl: './kroba-loader.component.html',
  styleUrls: ['./kroba-loader.component.scss'],
})
export class KrobaLoaderComponent implements OnInit, OnChanges {
  @Input() loaded: boolean;

  loading: any;
  loadingSub: any;


  constructor(public loadingController: LoadingController) { }

  async ngOnChanges(changes: SimpleChanges) {
    if (this.loading){
      const delay = ms => new Promise(res => setTimeout(res, ms));
      await delay(1000);
      console.log('Dismissing');
    }
    this.checkLoaded(this.loaded).then(r => {}).catch(e => {
      this.loading.dismiss();
      console.log('Error:', e);
    });
  }

  async checkLoaded(loaded) {
    if (loaded === true && this.loading) {
      console.log('Dissmising loading: ', loaded);
      this.loading.dismiss();
      this.loading = null;
    } else if (loaded === false) {
      await this.presentLoading();
    }
  }
  async presentLoading() {
    const loading = await this.loadingController.create({
      cssClass: 'my-custom-class',
      message: 'Please wait...',
      duration: 50000,
    });
    await loading.present();
    this.loading = loading;
    const { role, data } = await loading.onDidDismiss();
  }

  ngOnInit(): void {
  }
}
